.appContainer {
  display: flex;
  flex: 1;
  width: 100%;
  height: 100vh;
  background: linear-gradient(
    90deg,
    rgba(248, 248, 248, 1),
    rgba(252, 247, 234, 1),
    rgba(243, 245, 227, 1),
    rgba(216, 244, 232, 1)
  );
  justify-content: space-around;
  padding: 2rem;
}

.contentsRight {
  flex-grow: 1;
  padding: 0;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  flex: 0.95;
  padding: 0.5rem 2rem;
  background: white;
  height: calc(100vh - 4rem);
  border-radius: 10px;
}

.dashboardFlex {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}

.content {
  width: 100%;
  display: flex;
  flex-basis: 32%;
  border: 1px solid gainsboro;
  border-radius: 10px;
  min-height: 5rem;
  flex-direction: column;
}
.borderless {
  border: none;
}

.btnContainer {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 1rem;
}
.right {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
}
.btn {
  height: 3.5rem;
  outline: none;
  padding: 1rem 3rem;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  border: none;
  outline: none;
  align-items: center;
  margin-right: 2rem;
}

.btnSM {
  height: 2.5rem;
  outline: none;
  padding: 1rem 2rem;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  border: none;
  outline: none;
  align-items: center;
  margin-right: 1rem;
}

.btn__white {
  background: white;
  /* box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px; */
  color: #43b8b1;
  border: 1px solid #43b8b1;
}
.btn__gray{
  background: white;
  /* box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px; */
  color: gray;
  border: 1px solid gray
}

.InputFlex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  flex: 1;
}
